* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
}

h1 {
  text-align: center;
  margin: 1rem;
}

ol {
  display: grid;
  grid: repeat(5, 1fr) / repeat(4, 1fr)
}

li {
  display: flex;
  flex-direction: column;
}